	module.exports = {"data":{"markdownRemark":{"html":"<h2>What is R?</h2>\n<p>R is an open source programming language and software environment for statistical computing and graphics. It is one of the primary languages used by data scientists and statisticians alike. It is supported by the R Foundation for Statistical Computing and a large community of open source developers. </p>\n<h2>Popular R Tools &#x26; Packages</h2>\n<ul>\n<li><a href=\"https://www.rstudio.com/products/rstudio/\">R Studio:</a></li>\n<li>RStudio is an integrated development environment (IDE) for R. It includes a console, syntax-highlighting editor that supports direct code execution, as well as tools for plotting, history, debugging and workspace management. <a href=\"https://cran.r-project.org/\">The Comprehensive R Archive Network</a></li>\n<li>CRAN is a leading source for R tools and resources. Source code for many top open-source R packages can be found here.</li>\n</ul>\n<h2>Where to learn R for free</h2>\n<ul>\n<li><a href=\"https://www.rstudio.com/online-learning/\">R Studio</a></li>\n<li><a href=\"http://tryr.codeschool.com/\">Code school</a></li>\n<li><a href=\"https://www.coursera.org/learn/r-programming\">Coursera</a></li>\n<li><a href=\"https://www.datacamp.com\">DataCamp</a></li>\n</ul>","fields":{"slug":"/r/"},"frontmatter":{"title":"R"}}},"pathContext":{"slug":"/r/"}}

